
.main_wrapper {
  padding-top: 30px;
}
a {
  text-decoration: none;
  color: #000;
}
a:link,
a:visit,
a:hover,
a:active {
  color: #000;
  text-decoration: none;
}
.index_page__content_section {
  margin: auto;
  width: 1200px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.example {
  width: 370px;
  border: 1px solid #cecece;
  background-color: #fff;
  border-radius: 6px;
  -webkit-box-shadow: 1px 1px 5px 1px #ccc;
          box-shadow: 1px 1px 5px 1px #ccc;
}
.example__cover_box {
  width: 100%;
  height: 200px;
  background-color: #000;
}
.example__cover {
  display: block;
  width: 100%;
  height: 100%;
}
.example__info {
  padding: 0 15px 10px;
}
.example__info__title {
  font-size: 21px;
  color: #555;
  width: 340px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.example__info__title:hover {
  color: #4e9815;
}
.example__info__description {
  color: #999;
  font-size: 15px;
}
.preloader__info_box {
  margin: auto;
}
.preloader__info {
  text-align: center;
  font-size: 18px;
}
.preloader__info_strong {
  font-weight: bold;
}